import React from "react";
import PageLayout from "../../components/PageLayout/Layout";
import { PURCHASE_ORDER_DROPDOWNS } from "../../utils/formdropdown_grid.data";
import { PURCHASE_ORDER_FORM } from "../../utils/form_grid.data";
import { PURCHASE_ORDER_SCHEMA } from "../../utils/states.values";
import { PURCHASE_ORDER_GRID } from "../../utils/table_header.data";

const PurchaseOrder = () => {
  return (
    <PageLayout
      title={"Purchase Order"}
      URL={"/api/purchase_orders"}
      pageGrid={PURCHASE_ORDER_GRID}
      formGrid={PURCHASE_ORDER_FORM}
      valuesGroup={PURCHASE_ORDER_SCHEMA}
      formDropdownGrid={PURCHASE_ORDER_DROPDOWNS}
      searchKey={"po_num"}
      moreDataKey={"po_detail"}
    />
  );
};

export default PurchaseOrder;
