import React, { useState } from "react";
import { FaWindowClose } from "react-icons/fa";
import { FiChevronDown } from "react-icons/fi";

const ViewTableGrid = ({ type, gridData, onClose }) => {
  const [openedTab, setOpenedTab] = useState(null);
  const handleClose = () => onClose([]);
  const keyList = [
    "quantity",
    "part_number",
    "item_number",
    "vat_total",
    "line_total",
    "product_description",
    "created_at",
    "updated_at",
  ];
  return (
    <div className="bg-white w-800 ">
      <div className="flex justify-between items-center w-full px-5 py-2 bg-bluef">
        <h1 className="text-yellowfc text-xl font-bold uppercase">
          {type} Detail
        </h1>
        <FaWindowClose
          className="text-yellowfc cursor-pointer"
          onClick={handleClose}
        />
      </div>
      {gridData.map((item, index) => (
        <div key={index} className={"w-full p-2"}>
          <div
            className="flex items-center justify-between p-2 rounded-md bg-bluef text-white cursor-pointer  "
            onClick={() => setOpenedTab(openedTab !== null ? null : index)}
          >
            <h6 className="font-semibold text-md">Order {index + 1}</h6>
            <FiChevronDown />
          </div>
          {openedTab === index && (
            <table className="my-3 border-1 w-full">
              <thead>
                <tr className="bg-gray-300 text-left h-10">
                  <th className="text-sm text-gray-700 capitalize pl-2">label</th>
                  <th className="text-sm text-gray-700 capitalize">detail</th>
                </tr>
              </thead>
              <tbody>
                {Object.entries(item).map(
                  ([key, value]) =>
                    keyList.includes(key) && (
                      <tr key={key + value} className="border-b-1 h-10">
                        <td className="w-max text-sm text-bluefc pl-2">{key}</td>
                        <td className="w-max text-sm text-bluefc ">{value}</td>
                      </tr>
                    )
                )}
              </tbody>
            </table>
          )}
        </div>
      ))}
    </div>
  );
};

export default ViewTableGrid;
