import React from "react";

const Modal = ({ children }) => {
  return (
    <div
      className={`absolute justify-center items-start flex overflow-hidden inset-0
       z-50 outline-none focus:outline-none bg-black bg-opacity-50 p-5`}
    >
      <div
        className={`bg-white max-h-90 overflow-auto relative inset-0 max-w-8% w-auto rounded-lg`}
      >
        {children}
      </div>
    </div>
  );
};

export default Modal;
