import React from "react";
import PageLayout from "../../components/PageLayout/Layout";
import { useStateContext } from "../../context/ContextProvider";
import { INVOICE_DROPDOWNS } from "../../utils/formdropdown_grid.data";
import { INVOICES_FORM } from "../../utils/form_grid.data";
import { INVOICE_SCHEMA } from "../../utils/states.values";
import { INVOICE_GRID } from "../../utils/table_header.data";

const Invoice = () => {
  let { sessionInfo } = useStateContext();
  INVOICE_SCHEMA["user_id"] = JSON.parse(sessionInfo)?.id;
  return (
    <PageLayout
      title={"Invoice"}
      URL={"/api/invoices"}
      pageGrid={INVOICE_GRID}
      formGrid={INVOICES_FORM}
      valuesGroup={INVOICE_SCHEMA}
      formDropdownGrid={INVOICE_DROPDOWNS}
      searchKey={"inv_num"}
      moreDataKey={"invoice_detail"}
    />
  );
};

export default Invoice;
